<template>
  <div class="list-items">
    <Skeleton v-if="posts?.length < 1" type="recommend-movies" :count="4" />
    <div v-else class="list-item" v-for="item in posts" :key="item.id">
      <RCVideoItem :video="item" />
    </div>
    <Skeleton v-if="loadingMore" type="recommend-movies" :count="1" />
    <div v-else-if="hasMorePages" class="loadmore-btn" @click="loadMore">
      点击加载更多
    </div>
  </div>
</template>

<script lang="ts">
import { ref, toRefs, watch, computed } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { COLLECTION_POSTS_QUERY } from '@/graphql';
import { mergeProperty } from '@/common';
import Skeleton from '@/components/Skeleton.vue';
import RCVideoItem from '@/components/RCVideoItem.vue';

export default {
  props: {
    collection: {
      type: Object
    }
  },
  components: {
    Skeleton,
    RCVideoItem
  },
  setup(props) {
    const { collection } = toRefs(props);
    const { result, loading, error, fetchMore } = useQuery(COLLECTION_POSTS_QUERY, {
      id: collection.value?.id,
      page: 1,
      count: 4
    });
    const posts = useResult(result, [], (data: any) => data?.collection?.posts?.data);
    const paginatorInfo = useResult(result, {}, (data: any) => data?.collection?.posts?.paginatorInfo);
    const currentPage = ref(1);
    const hasMorePages = ref(true);

    watch(paginatorInfo, (value, prevValue) => {
      currentPage.value = value?.currentPage;
      hasMorePages.value = value?.hasMorePages;
    });

    const loadingMore = ref(false);
    const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

    function loadMore() {
      if (hasMorePages.value && !loadingMore.value) {
        loadingMore.value = true;
        fetchMore({
          variables: {
            page: currentPage.value + 1
          },
          updateQuery: (prev: any, { fetchMoreResult }) => {
            loadingMore.value = false;
            if (!fetchMoreResult) {
              return prev;
            }
            return mergeProperty(prev, fetchMoreResult);
          }
        });
      }
    }

    return {
      loading,
      error,
      disabled,
      loadMore,
      loadingMore,
      hasMorePages,
      collection,
      posts
    };
  }
};
</script>

<style lang="scss" scoped>
.list-item {
  display: block;
  margin-top: 20px;
}
.loadmore-btn {
  margin-top: 20px;
  width: 100%;
  border: 1px solid #eff0f3;
  border-radius: 4px;
  color: #999;
  font-size: 12px;
  line-height: 32px;
  text-align: center;
  background-color: transparent;
  cursor: pointer;
}
</style>
