
import { ref, watch } from 'vue';
import { useRoute } from 'vue-router';
import { useQuery, useResult } from '@vue/apollo-composable';
import { usePostQuery, FAST_RECOMMEND_POSTS_QUERY } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import DPlayer from '@/components/player/DPlayer.vue';
import CommentModule from '@/components/comment/CommentModule.vue';
import RCVideoItem from '@/components/RCVideoItem.vue';
import VideoToolbar from './parts/VideoToolbar.vue';
import AuthorInfo from './parts/AuthorInfo.vue';
import CollectionPosts from './parts/CollectionPosts.vue';

export default {
  name: 'VideoDetail',
  components: {
    DPlayer,
    CommentModule,
    RCVideoItem,
    Skeleton,
    AuthorInfo,
    VideoToolbar,
    CollectionPosts
  },

  setup(props) {
    const route = useRoute();
    const postId = ref(Number(route.params?.id));
    const { post, collections } = usePostQuery({ id: postId });
    const collection = ref(collections.value?.[0]);
    watch(route, (value, prevValue) => {
      postId.value = Number(value.params?.id);
    });
    watch(collections, (value, prevValue) => {
      collection.value = collections.value?.[0];
    });
    const { result } = useQuery(FAST_RECOMMEND_POSTS_QUERY);
    const recommendPosts = useResult(result, [], (data: any) => data?.fastRecommendPosts?.slice(0, 4));

    return {
      post,
      collection,
      recommendPosts
    };
  },
  methods: {
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    }
  },
  data() {
    return {
      isCollapse: true
    };
  }
};
