<template>
  <Masthead />
  <div class="video-play">
    <div class="play-detail">
      <div class="mod-main">
        <div class="player-wrap">
          <div class="embed-responsive">
            <div class="player-container">
              <DPlayer v-if="post?.video" :source="post?.video" :progress="0" />
            </div>
          </div>
        </div>
        <div class="media-wrap">
          <div :class="['movie-intro', 'hide-lg', isCollapse && 'collapse']">
            <div class="intro-text">
              {{ post?.description }}
              <span class="down-up up-box" @click="toggleCollapse"
                >收起
                <i class="el-icon-arrow-up"></i>
              </span>
            </div>
            <span class="down-up down-box" @click="toggleCollapse"
              >展开
              <i class="el-icon-arrow-down"></i>
            </span>
          </div>
          <VideoToolbar v-if="post" :post="post" />
          <div class="mod-wrap show-lg">
            <div class="mod-title">
              <h3>
                推荐视频
              </h3>
            </div>
            <div class="list-items">
              <Skeleton v-if="recommendPosts?.length < 1" type="recommend-movies" :count="4" />
              <div v-else class="list-item" v-for="item in recommendPosts" :key="item?.id">
                <RCVideoItem :video="item" />
              </div>
            </div>
          </div>
          <CommentModule v-if="post?.id" :commentable-id="post?.id" type="posts" />
        </div>
      </div>
      <div class="mod-side">
        <div class="mod-wrap">
          <AuthorInfo :user="post?.user" />
          <div class="divider"></div>
        </div>
        <div class="mod-wrap" v-if="collection?.id">
          <div class="mod-header">
            <h3 class="mod-title">
              {{ collection?.name || '相关视频' }}
            </h3>
            <div v-if="collection?.count_posts" class="subtitle">共{{ collection?.count_posts }}集</div>
          </div>
          <div v-if="collection?.description" class="mod-desc">{{ collection?.description }}</div>
          <CollectionPosts :collection="collection" />
        </div>
        <div class="mod-wrap">
          <div class="mod-title">
            <h3>
              推荐视频
            </h3>
          </div>
          <div class="list-items">
            <Skeleton v-if="recommendPosts?.length < 1" type="recommend-movies" :count="4" />
            <div v-else class="list-item" v-for="item in recommendPosts" :key="item?.id">
              <RCVideoItem :video="item" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
  <SideBar />
</template>

<script lang="ts">
import { ref, watch } from 'vue';
import { useRoute } from 'vue-router';
import { useQuery, useResult } from '@vue/apollo-composable';
import { usePostQuery, FAST_RECOMMEND_POSTS_QUERY } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import DPlayer from '@/components/player/DPlayer.vue';
import CommentModule from '@/components/comment/CommentModule.vue';
import RCVideoItem from '@/components/RCVideoItem.vue';
import VideoToolbar from './parts/VideoToolbar.vue';
import AuthorInfo from './parts/AuthorInfo.vue';
import CollectionPosts from './parts/CollectionPosts.vue';

export default {
  name: 'VideoDetail',
  components: {
    DPlayer,
    CommentModule,
    RCVideoItem,
    Skeleton,
    AuthorInfo,
    VideoToolbar,
    CollectionPosts
  },

  setup(props) {
    const route = useRoute();
    const postId = ref(Number(route.params?.id));
    const { post, collections } = usePostQuery({ id: postId });
    const collection = ref(collections.value?.[0]);
    watch(route, (value, prevValue) => {
      postId.value = Number(value.params?.id);
    });
    watch(collections, (value, prevValue) => {
      collection.value = collections.value?.[0];
    });
    const { result } = useQuery(FAST_RECOMMEND_POSTS_QUERY);
    const recommendPosts = useResult(result, [], (data: any) => data?.fastRecommendPosts?.slice(0, 4));

    return {
      post,
      collection,
      recommendPosts
    };
  },
  methods: {
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    }
  },
  data() {
    return {
      isCollapse: true
    };
  }
};
</script>

